<template>
  <div class="user-container">
       <svg xmlns="http://www.w3.org/2000/svg" version="1.1" id="svgBox">
          <defs>
            <linearGradient id="linear" x1="0%" y1="0%" x2="100%" y2="0%">
              <stop offset="0%"   stop-color="rgba(255,255,255,0.3)" />
              <stop offset="100%" stop-color="rgba(255,255,255,0.1)" />
            </linearGradient>
          </defs>
          <rect x="0" y="0"  width="280" height="180" class="rectBox" />
        </svg>
       <section class="head-icon" @click="animateMotion">
          <img :src="userInfos.img" class="head-icon-img"/>
       </section><!-- end header icon -->
       <section class="user-info">
          <div class="user-info-content">
            <span class="gotoLogin" v-show="!userExist">亲 ! 您还未登录，请 <i class="loginLink" @click="gotoLogin">登录</i>！</span>
            <ul class="user-list-wrap" v-show="userExist">
              <li class="userList">Name: {{userInfos.name}}</li>
              <li class="userList">Email: {{userInfos.email}}</li>
              <li class="userList">Phone: {{userInfos.phone}}</li>
              <li class="userList">Intro: {{userInfos.intro}}</li>
            </ul>
          </div>
          <div class="user-info-btn">
            <span class="ui-btn pick-btn" @click="animateMotion"><font>PICK</font></span>
            <span class="ui-btn exit-btn" @click="gotoLogin"><font>EXIT</font></span>
          </div>
       </section>
  </div>
</template>

<style type="text/css">
  @keyframes dash{
    form{
      stroke-dashoffset: 920;
      stroke-opacity:0.2;
    }to{
      stroke-dashoffset: 0;
      stroke-opacity:0.9;
    }
  }

  @keyframes dashBack{
    form{
      stroke-dashoffset: 0;
    }to{
      stroke-dashoffset: 920;
    }
  }
        
</style>

<script scoped>
  
  import toolBrowser from 'common/js/tool.browser.js';
  require('common/js/jquery.easing.js')

  export default {
    data(){
      return {
        userInfo:{},
        userExist: false,
        isAnimate: true,
        isEventAnimate: true,
        userAnimationOnoff: true,
        url: '/mcm/api/user/',
        userInfos: {
          name: 'movi',
          email: '928091232@qq.com',
          phone: '13396529752',
          intro: '喜欢看电影，听音乐！',
          img: 'http://8072l2.com1.z0.glb.clouddn.com/apicloud/8847dbe75e90b6b444beb14c007b0862.jpeg'
        },
        userInfoFormat:{}
      }
    },
    methods:{
      animateMotion(){

        if(!this.userAnimationOnoff){
          return;
        }

        var svgBox = document.getElementById('svgBox');
        var rectBox = svgBox.getElementsByClassName('rectBox')[0];
        rectBox.style.cssText = '';

        if(this.isAnimate){

          $('.head-icon').css('transform','rotate(-360deg)');
          rectBox.style.strokeDashoffset = '920';

          var timer4 = setTimeout(()=>{
            rectBox.style.animation = 'dash 1.8s ease-out forwards';
            clearTimeout(timer4);
          },1000);

          var timer2 = setTimeout(()=>{
            //1.内容出现动画
            $('.user-list-wrap').css('opacity',1);
            
            //2.列表动画
            this.$velocity($('.userList'),"transition.flipBounceXIn", { stagger: 200 ,complete:()=>{
              $('.user-info-btn').css('opacity',1);
              this.$velocity($('.ui-btn'),"transition.bounceIn", { stagger: 200 ,complete:()=>{
                this.userAnimationOnoff = true;
              }});
            }});

          },1800);

        }else{
          
          rectBox.style.strokeDashoffset = '0';
          rectBox.style.animation = 'dashBack 1.8s ease-out forwards';
          

          var timer = setTimeout(()=>{
            this.$velocity($('.userList'),"transition.flipBounceXOut", { stagger: 200 ,complete:()=>{
              $('.user-info-btn').css('opacity',1);
            }});
            this.$velocity($('.ui-btn'),"transition.bounceOut", { stagger: 200 ,delay: 200,complete:()=>{
              }});
            clearTimeout(timer);
          },1000);


          var timer3 = setTimeout(()=>{
            $('.head-icon').css('transform','rotate(360deg)');
/*            $('.user-container').animate({'right':-280},1000,'easeInOutQuint',()=>{
              $('.user-list-wrap').css('opacity',0);
              $('.user-info-btn').css('opacity',0);
            });*/
            var timer5 = setTimeout(()=>{
              $('.user-list-wrap').css('opacity',0);
              $('.user-info-btn').css('opacity',0);
              clearTimeout(timer5);
              this.userAnimationOnoff = true;
            },1000);

          },2500);
        }
        
        this.userAnimationOnoff = false;
        this.isAnimate = !this.isAnimate;
        this.$emit('motionstart');
      },
      initUserData(){
        //检测本地用户数据
        let userData = toolBrowser.lget('loginInfo');
        if(!userData){
          this.userExist = false;
        }

        //如果存在本地用户
        this.userExist = true;
        let userId = userData.userId;

        if(toolBrowser.lget('userInfo')){
          this.userInfoFormat = toolBrowser.lget('userInfo');
          this.userInfos = this.formatData(this.userInfoFormat);
        }else{
          return
          //请求用户数据
          this.$axios.get(this.$host+this.url+userId).then((res)=>{
            toolBrowser.lset('userInfo',res.data);
            this.userInfoFormat = res.data;
            this.userInfos = this.formatData(this.userInfoFormat);
          }).catch((e)=>{
            console.log(e);
          });
        }    

      },
      formatData(data){
        var d = {
          name:'',
          email:'',
          phone:'',
          intro:'',
          img: ''
        };
        d.name = data.alias;
        d.email = data.username;
        d.phone = data.mobile;
        d.intro = data.intro;
        d.img = data.cover.url;

        return d;
      },
      //去到登录页面
      gotoLogin(){
        this.$router.push({'path':'/login'});
      }
    },
    created(){
      this.initUserData();
    },
    mounted(){

    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only @import './login.styl' -->
<style scoped lang="stylus">
    @import './stylus/user.styl'
</style>


